



























































































































































































































































import { Component, Vue } from "vue-property-decorator";
import { papers } from "@/data/papers";
import { projectConfig } from "@/data/project";
import { State } from "@/store";
import HelloWorld from "@/components/HelloWorld.vue"; // @ is an alias to /src
import PaperCard from "@/components/PaperCard.vue";
import DetailCard from "@/components/DetailCard.vue";
import PaperDetail from "@/interfaces/Paper";

@Component({
  components: {
    HelloWorld,
    PaperCard,
    DetailCard
  }
})
export default class Home extends Vue {
  startDate: string = projectConfig.startDate;
  indeterminate: boolean = true;
  show: boolean = false;
  paper: PaperDetail = {
    download: "",
    keywords: [],
    methodology: "",
    reflection: "",
    solvedProblem: "",
    source: "",
    title: "",
    finishDate: ""
  };
  snackbar: boolean = false;

  get allPapers() {
    return this.$store.state.papers;
  }

  get showAlert(): boolean {
    return !(this.allPapers.length > 0);
  }

  get shouldReadLicense() {
    return (this.$store.state as State).config.readLicense;
  }
  get progress(): number {
    return (this.allPapers.length / 10) * 100;
  }
  openDetailCard(index: number) {
    this.paper = this.allPapers[index];
    this.show = true;
  }
  snackbarConfirm() {
    this.$store.commit("INIT_PAPER");
    this.$store.commit("READ_LICENSE");
    this.snackbar = false;
  }
  mounted() {
    if (this.shouldReadLicense) {
      setTimeout(() => {
        this.snackbar = true;
      }, 1000);
    }
  }
}
