import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from "vuex-persistedstate";
import PaperDetail from "@/interfaces/Paper";

Vue.use(Vuex);

interface Config {
  darkTheme: boolean;
  readLicense: boolean;
}
export interface State {
  config: Config;
  papers: PaperDetail[]
}

const defaultState: State = {
  config: {
    darkTheme: false,
    readLicense: true
  },
  papers: []
};

export default new Vuex.Store({
  state: defaultState,
  mutations: {
    INIT_PAPER(state: State) {
      state.papers.push(
        {
          download: 'https://unoiou.com/papers.pdf',
          keywords: ['Deep Learning'],
          methodology: '解决了一个问题',
          reflection: '这个问题实际上没有多少',
          solvedProblem: '岭回归',
          source: 'AAAI 2019',
          title: 'Distant unsupervised learning in spatio temporal database.',
          finishDate: '2019-03-30'
        },
      )
    },
    READ_LICENSE(state: State) {
      state.config.readLicense = false;
    }
  },
  actions: {},
  plugins: [createPersistedState()]
});
